import React, { useState, useEffect } from 'react';

import styled from 'styled-components';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import { HuePicker } from 'react-color';

import mixins from '../../helpers/mixins';

const SketchpadWrapper = styled.div`
    ${mixins.flexAlignCenter};
    flex-direction: row;
`;

const SketchpadCanvas = styled.canvas`
    display: block;
    
    background: #ffffff;
    border: 0.25em solid black;
    box-shadow: 0.25em 0.25em black;
    
    cursor: crosshair;
`;

const PickerWrapper = styled.div`
    position: absolute;
    bottom: -30px;
`;

const Sketchpad = () => {
    const [color, setColor] = useState('#ff0000');
    const handleChange = (color) => {
        setColor(color.hex);
    };

    useEffect(() => {
        const canvas = document.getElementById('signature-canvas');
        const context = canvas.getContext('2d');

        context.strokeStyle = color;
        context.lineWidth = 6;
        context.lineCap = 'round';

        let lastEvent;
        let drawing = false;

        const mouseDownEvent = e => {
            lastEvent = e;
            drawing = true;
        };

        const mouseMoveEvent = e => {
            if (drawing) {
                context.beginPath();

                context.moveTo(lastEvent.offsetX, lastEvent.offsetY);
                context.lineTo(e.offsetX, e.offsetY);

                context.stroke();
                context.closePath();

                lastEvent = e;
            }
        };

        const mouseUpEvent = () => {
            drawing = false;
        };

        const getTouchPos = e => {
            let touchX = 0;
            let touchY = 0;

            if (e.touches) {
                if (e.touches.length === 1) {
                    const touch = e.touches[0];
                    const rect = canvas.getBoundingClientRect();

                    touchX = touch.clientX - rect.left;
                    touchY = touch.clientY - rect.top;
                }
            }

            return [touchX, touchY];
        };

        const touchStartEvent = e => {
            lastEvent = e;
            drawing = true;
        };

        const touchMoveEvent = e => {
            context.beginPath();

            context.moveTo(getTouchPos(lastEvent)[0], getTouchPos(lastEvent)[1]);
            context.lineTo(getTouchPos(e)[0], getTouchPos(e)[1]);

            context.stroke();
            context.closePath();

            lastEvent = e;
        };

        const touchEndEvent = () => {

        };

        canvas.addEventListener('mousedown', mouseDownEvent);
        canvas.addEventListener('mousemove', mouseMoveEvent);
        canvas.addEventListener('mouseup', mouseUpEvent);

        canvas.addEventListener('touchstart', touchStartEvent);
        canvas.addEventListener('touchmove', touchMoveEvent);
        canvas.addEventListener('touchend', touchEndEvent);
    }, [color]);

    return (
        <SketchpadWrapper>
            <SketchpadCanvas height={250} width={250} id={'signature-canvas'} />

            <PickerWrapper>
                <HuePicker
                    width={200}
                    color={color}
                    onChange={handleChange}
                />
            </PickerWrapper>
        </SketchpadWrapper>
    );
};

export default Sketchpad;
